import { useEffect } from "react";
import iconRetinaUrl from "leaflet/dist/images/marker-icon-2x.png";
import iconUrl from "leaflet/dist/images/marker-icon.png";
import shadowUrl from "leaflet/dist/images/marker-shadow.png";
import "leaflet/dist/leaflet.css";
import { MapContainer, TileLayer, GeoJSON, Tooltip } from "react-leaflet";
import { Col, Row, Typography } from "antd";

const CMap = ({ data }) => {
  const { Text, Title } = Typography;
  const center = [-1.681488, 113.382355];

  useEffect(() => {
    replaceIcon();
  }, []);

  const replaceIcon = async () => {
    delete L.Icon.Default.prototype._getIconUrl;
    L.Icon.Default.mergeOptions({
      iconRetinaUrl: iconRetinaUrl.src,
      iconUrl: iconUrl.src,
      shadowUrl: shadowUrl.src,
    });
  };

  const colors = {
    ACEH: "#0000FF",
    BALI: "blue",
    BANTEN: "#DC143C",
    BENGKULU: "#8B0000",
    "DI YOGYAKARTA": "#9ACD32",
    "DKI JAKARTA": "#9400D3",
    GORONTALO: "yellow",
    JAMBI: "red",
    "JAWA BARAT": "#945a11",
    "JAWA TENGAH": "#FFD700",
    "JAWA TIMUR": "#808000",
    "KALIMANTAN BARAT ": "red",
    "KALIMANTAN SELATAN": "yellow",
    "KALIMANTAN TENGAH": "purple",
    "KALIMANTAN TIMUR": "green",
    "KALIMANTAN UTARA": "blue",
    "SUMATRA UTARA": "orange",
    "KEPULAUAN BANGKA BELITUNG": "yellow",
    "KALIMANTAN RIAU": "grey",
    LAMPUNG: "pink",
    MALUKU: "black",
    "MALUKU UTARA": "yellow",
    "NUSA TENGGARA BARAT": "red",
    "NUSA TENGGARA TIMUR": "purple",
    PAPUA: "green",
    "PAPUA BARAT": "blue",
    RIAU: "orange",
    "SULAWESI BARAT": "brown",
    "SULAWESI TENGAH": "pink",
    "SULAWESI TENGGARA": "#94D3D3",
    "SULAWESI UTARA": "yellow",
    "SUMATERA BARAT": "red",
    "SUMATERA SELATAN": "#D2691E",
  };
  const renderCountProvince = (data) => {
    return data.map((item) => {
      console.log(item.properties);
      let key = item.properties.id;
      return (
        <Row
          key={key}
          style={{
            width: "100%",
            marginTop: -4,
            marginBottom: -20,
          }}
          gutter={4}
        >
          <Col span={3}>
            <Title
              style={{
                backgroundColor: colors[item.properties.name],
                width: "15px",
                height: "15px",
                marginTop: 4,
              }}
            >
              &nbsp;
            </Title>
          </Col>
          <Col span={14}>
            <Text>{item.properties.name}</Text>
          </Col>
          <Col span={7}>
            <Text style={{ float: "right" }}>{item.properties.total}</Text>
          </Col>
        </Row>
      );
    });
  };
  const renderCountries = (data) => {
    return data.map((feature) => {
      let key = feature.properties.id;
      let style = {
        weight: 2,
        color: colors[feature.properties.name],
        opacity: 1,
      };
      return (
        <GeoJSON key={key} data={feature.geometry} style={style}>
          <Tooltip permanent sticky>
            <div>Provinsi: {feature.properties.name}</div>
            <div>Total Asset: {feature.properties.total}</div>
          </Tooltip>
        </GeoJSON>
      );
    });
  };

  return (
    <>
      {data.length > 0 && (
        <div style={{ position: "relative" }}>
          <Row
            style={{
              position: "absolute",
              bottom: "12px",
              left: "12px",
              zIndex: 1000,
              background: "rgba(255, 255, 255, 0.8)",
              padding: "5px",
              fontSize: "12px",
            }}
          >
            <Row style={{ width: "200px", lineHeight: "15px" }}>
              {renderCountProvince(data)}
            </Row>
          </Row>
        </div>
      )}
      <MapContainer
        center={center}
        zoom={5}
        style={{ height: "500px", width: "100%" }}
      >
        <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />
        {data.length > 0 && renderCountries(data)}
      </MapContainer>
    </>
  );
};
export default CMap;
